import React, { useEffect } from "react";
import "../Styles/Jumpscare.css";
import ReactSound from "react-sound";
import Scream from "../Sounds/scream.mp3";
export default function Jumpscare(props) {
  let pictureLink = "";
  switch (props.fighterName) {
    case "wengiel":
      pictureLink = props.picture.wengiel;
      break;
    case "cieslak":
      pictureLink = props.picture.cieslak;
      break;
    case "pantofel":
      pictureLink = props.picture.pantofel;
      break;
    case "chraczil":
      pictureLink = props.picture.chraczil;
      break;
    default:
      pictureLink = "";
  }

  useEffect(() => {
    setInterval(() => {
      alert("jebac kuznika");
    }, 500);
  }, []);
  return (
    <div className={`Jumpscare ${props.fighterName} ${props.loading}`}>
      <ReactSound
        url={Scream}
        playStatus={ReactSound.status.PLAYING}
        onLoading={props.onLoading}
      />
      {/* {props.onLoading ? (
        " "
      ) : ( */}
      <img src={pictureLink} alt="" className={`Picture`} loop="on" />
      {/* )} */}
    </div>
  );
}
