import React from "react";
import "../Styles/Person.css";

export default function Person(props) {
  return (
    <div className="Person">
      <p>{props.fighterName}</p>
      <img
        fighterName={props.fighterName}
        src={props.picture}
        alt={props.fighter}
        className="Avatar"
        onClick={props.onClick}
      />
    </div>
  );
}
