import { useState } from "react";
import "./App.css";
import Person from "./Components/Person";
import Jumpscare from "./Components/Jumpscare";
import images from "./Images/Images";

export default function App() {
  const [fighter, setFighter] = useState("");
  const [loading, setLoading] = useState(false);
  const handleClick = (e) => {
    setFighter(e.target.getAttribute("fighterName"));
  };

  // const handleLoadingSound = () => {
  //   setLoading(true);
  // };

  if (fighter === "") {
    return (
      <div className="App">
        Choose your fighter:
        <Person
          fighterName={"pantofel"}
          picture={images.pantofel}
          onClick={handleClick}
        />
        <Person
          fighterName="wengiel"
          picture={images.wengiel}
          onClick={handleClick}
        />
        <Person
          fighterName={"chraczil"}
          picture={images.chraczil}
          onClick={handleClick}
        />
        <Person
          fighterName={"cieslak"}
          picture={images.cieslak}
          onClick={handleClick}
        />
      </div>
    );
  } else {
    return (
      <div>
        <Jumpscare
          fighterName={fighter}
          picture={images}
          // onLoading={handleLoadingSound}
          loading={loading}
        />
      </div>
    );
  }
}
